<template>
  <user>
    <html-head 
      v-if="user.data.name"
      :title="user.data.name"
    ></html-head>

    <page-header
      :to="{ name: 'users.index' }"
    >
      <template v-slot:title >
        <span v-text="`User Information`"></span>
      </template>
    </page-header>

    <v-row>
      <v-col cols="12" sm="8" lg="6" class="mx-auto">
        <show-skeleton v-if="user.isFetching"></show-skeleton>
        <v-card v-else elevation="0">
          <v-row>
            <v-col cols="auto" class="mx-auto">
              <v-avatar
                size="164"
                tile
                class="ma-7"
              >
                <img :src="user.data.image" />
              </v-avatar>
            </v-col>
          </v-row>
          <v-card-title v-text="`${ user.data.firstName } ${ user.data.lastName }`"></v-card-title>
          <v-card-subtitle v-text="`${ user.data.roleDetails.name }`"></v-card-subtitle>
          <v-card-text>
            <p>E-mail: <span v-text="user.data.email"></span></p>
            <p>Registered: <span v-text="user.data.createdAt"></span></p>
          </v-card-text>
          <v-card-actions>
            <option-buttons
              :edit="true"
              :trash="true"
              name="user"
              :id="user.data._id"
            ></option-buttons>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </user>
</template>

<script>
import Api from './routes/api'
import User from './models/User'

export default {
  components: {
    ShowSkeleton: () => import( './cards/ShowSkeleton' )
  },

  data: () => ({
    user: new User
  }),

  mounted () {
    this.user.getResource( Api.alone( this.$route.params.id ) )
    this.user.load( false )
  }
}
</script>